.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.numbers{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
  margin-top: 160px;
}
.getButton{
  width: 200px;
  height: 40px;
}
.number{
  width: 300px;
  height: 50px;
  background-color: cadetblue;
  display: flex;
  justify-content: center;
  padding-top: 5px;
}
.showButton{
  width: 300px;
  height: 25px;
  border-radius: 9px;
}
.showBlock{
  width: 300px;
  display: flex;
  flex-direction: column;
  border: 1px solid black;
  border-radius: 10px;
  padding: 5px;
}
 .close{
  border-radius: 9px;
 }